.logo {
  max-width: max-content;
}

.logo__wrap--tablet {
  display: none;
}
.logo__wrap--desktop {
  display: none;
}

@media (min-width: 768px) {
  .logo__wrap--mobile {
    display: none;
  }
  .logo__wrap--tablet {
    display: block;
  }
  .logo__wrap--desktop {
    display: none;
  }
}
@media (min-width: 1440px) {
