.page {
  min-height: 100%;
  display: grid;
}

.page__field {
  margin: 0;
  padding: 0;
  border: none;
  border: 0;
  outline: 0;
}

.page__container {
  display: contents;
}

.page__body {
  margin: 0;
  min-height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  font-size: 16px;
  font-family: "BloggerSans", "Arial", sans-serif;
  z-index: -1;
}
.page__body--catalog {
  background-color: var(--very-light-blue);
}

.page-main {
  position: relative;
  z-index: -1;
}

.page__hidden {
  position: absolute;
  transform: scale(0);
}

.page__center {
  margin: 0 auto;
  width: 270px;
}
.page__center--full {
  width: 320px;
}

.page__container {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  z-index: -1;
}

.page__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page__img {
  max-width: 100%;
  height: auto;
  display: block;
}

.page__button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.page__auto-link {
  display: block;
  box-sizing: border-box;
  font-weight: 700;
  letter-spacing: 1px;
  color: var(--basic-blue);
  text-transform: uppercase;
  background-color: var(--white-yellow);
  border-radius: 25px;
}
.page__auto-link:hover {
  background-color: var(--white-orange);
}
.page__auto-link:active {
  opacity: 0.3;
}

.page__link-changed:hover {
  background-color: var(--white-orange);
}
.page__link-changed:active {
  opacity: 0.3;
}

.page__link {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
.page__link, .page__link:visited, .page__link:hover, .page__link:active {
  text-decoration: none;
  font-variant: inherit;
  font-family: inherit;
  cursor: pointer;
}

@media (min-width: 768px) {
  .page__center {
    width: 688px;
  }
  .page__center--full {
    width: 768px;
  }
  .page__center--tablet-except {
    width: 618px;
  }
}
@media (min-width: 1440px) {
  .page__body--form {
    background-color: var(--very-light-blue);
  }
  .page-main--catalog {
    margin-bottom: 37px;
  }
  .page__container {
    grid-template-columns: 1fr auto;
    column-gap: 83px;
  }
  .page__center {
    width: 1320px;
  }
  .page__center--full {
    width: 1320px;
  }
  .page__center--desktop-except {
    margin: 0;
    width: initial;
  }
}
.page-header {
  margin-bottom: -2px;
  width: 100%;
  font-family: inherit;
  min-height: var(--header-min-height);
  background-color: var(--basic-blue);
}
.page-header--scrolled {
  position: sticky;
  top: 0;
  background-color: var(--basic-white);
  animation-name: smooth-appearance;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-timing-function: ease;
  animation-duration: 0.5s;
  transition: all 1s;
}
@keyframes smooth-appearance {
  from {
    top: -200px;
  }
  to {
    top: 0;
  }
}
.page-header--scrolled .header__wrapper .header__line {
  background-color: var(--next-gray);
}

.header__input:checked ~ .page-header {
  margin-bottom: 1px;
  min-height: 495px;
  background-color: var(--basic-white);
  z-index: 10;
}

.header__wrapper {
  position: relative;
  margin-bottom: 1px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: var(--header-min-height) 1fr;
  box-sizing: border-box;
}

.header__logo {
  display: block;
  grid-area: 1/1/-1/2;
  padding-bottom: 0;
  align-self: center;
  z-index: 2;
}
.header__logo .logo__image {
  fill: var(--basic-white);
}

.header-info {
  display: none;
}

.menu-open {
  position: absolute;
  top: 27px;
  right: 0;
  display: block;
  width: 25px;
  height: 20px;
  cursor: pointer;
  z-index: 2;
  -webkit-tap-highlight-color: transparent;
}

.header__nav {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  max-height: 0;
  text-align: center;
  transform: translateY(100%);
  font-size: 0;
  overflow: hidden;
  background-color: transparent;
}

.header__input {
  display: none;
}
.header__input:checked ~ .page-header .header__nav {
  padding-top: 0;
  min-height: 495px;
  font-size: 16px;
  background-color: var(--basic-white);
  box-shadow: 30vw 0 0 0 var(--basic-white), -30vw 0 0 0 var(--basic-white);
  z-index: 10;
}
.header__input:checked ~ .page-header .logo__image {
  fill: var(--black-blue);
}
.header__input:checked ~ .page-header .header__line:first-of-type, .header__input:checked ~ .page-header .header__line:last-of-type {
  transform: rotate(45deg);
  top: 36px;
  width: 23px;
  height: 2px;
  background-color: var(--next-gray);
  transition: 0.5s transform;
}
.header__input:checked ~ .page-header .header__line:last-of-type {
  transform: rotate(-45deg);
}

.page-header--scrolled .header__wrapper .logo .logo__wrap .logo__image {
  fill: var(--black-blue);
}

.header__line {
  position: absolute;
  top: 30px;
  align-self: center;
  right: 0;
  width: 25px;
  height: 3px;
  background-color: var(--basic-white);
}
.header__line:last-of-type {
  top: 40px;
}

